function createData(name, calories, fat, carbs, protein) {
  return { name, calories, fat, carbs, protein };
}

// make this a function, takes in api data, outputs rows
function getAPIData() {
  var APIData_arr = [];
  var balance = 0;
  for (var i=0;i<APIData.length;i++) {
    if (APIData[i].contract_decimals > 0) {
            balance = parseInt(APIData[i].balance) / Math.pow(10, APIData[i].contract_decimals);
        } else {
            balance = parseInt(APIData[i].balance);
        }
    APIData_arr.push(createData(
    APIData[i].contract_name,
    APIData[i].contract_ticker_symbol,
    balance.toFixed(4),
    parseFloat(APIData[i].quote).toFixed(2),
    APIData[i].type));
  }
  return APIData_arr;
}

function TableComponent(props) {
  const showTable = props.showTable;
  const classes = useStyles();
  if (!showTable) return <></>;
  return (
    <>
    <TableContainer component={Paper}>
      <Table className={classes.table} aria-label="simple table">
        <TableHead>
          <TableRow>
            <TableCell>Token</TableCell>
            <TableCell align="right">Symbol</TableCell>
            <TableCell align="right">Balance&nbsp;</TableCell>
            <TableCell align="right">Fiat Value&nbsp;</TableCell>
            <TableCell align="right">Type&nbsp;</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {getAPIData().map((row) => (
            <TableRow key={row.name}>
              <TableCell component="th" scope="row">
                {row.name}
              </TableCell>
              <TableCell align="right">{row.calories}</TableCell>
              <TableCell align="right">{row.fat}</TableCell>
              <TableCell align="right">{row.carbs}</TableCell>
              <TableCell align="right">{row.protein}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer> <br/> <br/>
    </>
  )
}

